.section.map-with-info {
    position: relative;
    overflow: hidden;
}
.section.map-with-info .google-map {
    position: absolute;
    top: 0;
    left: 0;
}
.section.map-with-info .google-map .map-marker-label {
    position: absolute;
    color: blue;
    font-size: 16px;
    font-weight: bold;
}
.section.map-with-info .text {
    position: relative;
    background: $white;
    z-index: 2;
    width: 100%;
    margin: 180px 15px 30px;
}
@media (min-width: $(desktop)) {
    .section.map-with-info .text {
        margin: 60px 0;
        margin-left: calc(99.99% * (-2/12 * -1) - (30px - 30px * (-2/12 * -1)) + 30px) !important;
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 1/3 - (30px - 30px * 1/3));
    }
    .section.map-with-info .text:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .section.map-with-info .text:last-child {
        margin-right: 0;
    }
    .section.map-with-info .text:nth-child(3 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.section.map-with-info .text .title {
    background: $blue;
    text-align: center;
    padding: 10px;
}
.section.map-with-info .text .title h3 {
    font-size: 22px;
    font-weight: normal;
    color: $white;
}
.section.map-with-info .text .body {
    padding: 30px;
    font-family: 'Open Sans';
}
.section.map-with-info .text p {
    font-family: 'Open Sans';
}
.section.map-with-info .text a {
    color: $blue_light;
}
.section.map-with-info .text h4 {
    font-size: 18px;
    color: $black;
    tex-transform: uppercase;
}
.section.map-with-info .text .section {
    margin-bottom: 15px;
}
.section.map-with-info .text .button {
    -webkit-transition: box-shadow 0.2s;
    transition: box-shadow 0.2s;
    background: $blue_light;
    display: block;
    background: $blue_light;
    margin-bottom: 0;
    color: $white;
}
.section.map-with-info .text .button:hover {
    box-shadow: 0 0 0 4px $blue_light,
            inset 0 0 0 3px $white;
    color: #fff;
}
